import { CommonAPI } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const commonServices = {
  getCountryService: async (queryParams) => {
    try {
      const payload = {
        ...CommonAPI.country,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  getStateService: async (queryParams) => {
    try {
      const payload = {
        ...CommonAPI.state,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
