import { Region } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const regionsServices = {
  getRegionsList: async (queryParams) => {
    try {
      const payload = {
        ...Region.allRegion,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
