import { Hub } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const hubServices = {
  addHubService: async (bodyData) => {
    try {
      const payload = {
        ...Hub.addHub,
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  getHubList: async (queryParams) => {
    try {
      const payload = {
        ...Hub.allHub,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
