import { Brand } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const brandServices = {
  getBrandList: async (queryParams) => {
    try {
      const payload = {
        ...Brand.allBrand,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  addBrand: async (bodyData) => {
    try {
      const payload = {
        ...Brand.addBrand,
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  updateBrand: async (id, bodyData) => {
    try {
      const payload = {
        ...Brand.updateBrand(id),
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
