import { Link, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { Col, Dropdown, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Form, Formik } from "formik";
import { CommonButton, ModalComponent, SweetAlert } from "../UiElement";
import {
  checkValidData,
  enterOnlyNumericValue,
  getFullName,
  logger,
  modalNotification,
} from "../../utils";
import adminRouteMap from "../../routeControl/adminRouteMap";
import {
  commonServices,
  hubServices,
  subAdminAuthServices,
} from "../../services";
import { logout, selectUserData } from "../../redux/AuthSlice/index.slice";
import { Select, Input as TextInput } from "../Antd";
import validation from "./validation";

function AdminHeader({ menuToggle }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const userData = useSelector(selectUserData);
  const [loading, setLoading] = useState(false);
  const [isAlertVisible, setIsAlertVisible] = useState(false);
  const [countryData, setCountryData] = useState([]);
  const [stateData, setStateData] = useState([]);
  const [countryId, setCountryId] = useState("");

  const initialValues = {
    name: "",
    city: "",
    landlineNo: "",
    website: "",
    address2: "",
    address1: "",
    mobileNo: "",
    countryId: undefined,
    stateId: undefined,
  };

  const showSweetAlert = () => {
    setIsAlertVisible(true);
  };

  const accountLogout = async () => {
    try {
      const response = await subAdminAuthServices.logoutService();
      const { success, message } = response;
      if (success) {
        modalNotification({
          type: "success",
          message,
        });
        dispatch(logout(navigate));
        setLoading(false);
        return;
      }
    } catch (error) {
      logger(error);
    }
    setLoading(false);
  };

  const onConfirmAlert = () => {
    setLoading(true);
    accountLogout();
    setIsAlertVisible(false);
    if (!loading) {
      return false;
    }
  };

  const [addHub, setAddHub] = useState(false);
  const handleClose = () => {
    setAddHub(false);
  };

  const addHubDistributor = async (value) => {
    try {
      console.log("value", value);
      let bodyData = {
        ...value,
      };
      bodyData.stateId = "1";
      const res = await hubServices?.addHubService(bodyData);
      if (res?.success) {
        modalNotification({
          type: "success",
          message: res?.message,
        });
      }
    } catch (error) {
      logger(error);
    }
  };

  const getCountry = async () => {
    try {
      let queryParams = {};
      const res = await commonServices?.getCountryService(queryParams);

      if (res?.success) {
        setCountryData(res?.data?.rows);
      }
    } catch (error) {
      logger(error);
    }
  };

  const getState = async (id) => {
    try {
      let queryParams = {
        countryId: id,
      };
      const res = await commonServices?.getStateService(queryParams);
      if (res?.success) {
        setCountryId("");
        setStateData(res?.data?.rows);
      }
    } catch (error) {
      logger(error);
    }
  };

  useEffect(() => {
    getCountry();
  }, []);

  useEffect(() => {
    if (countryId) {
      getState(countryId);
    }
  }, [countryId]);

  return (
    <>
      <div className="nk-header nk-header-fixed is-light">
        <div className="container-fluid">
          <div className="nk-header-wrap">
            <div className="nk-menu-trigger d-xl-none ms-n1">
              <Link
                onClick={() => menuToggle()}
                to="#"
                className="nk-nav-toggle nk-quick-nav-icon ps-0"
              >
                <em className="icon ni ni-menu" />
              </Link>
            </div>
            <div className="nk-header-brand d-xl-none">
              <Link to={adminRouteMap.DASHBOARD.path} className="logo-link">
                <img className="logo-img" src="" alt="logo" />
              </Link>
            </div>
            <div className="d-flex align-items-center justify-content-between w-100">
              <div>
                <div className="d-flex align-items-center">
                  <div className="form-group mb-0">
                    <input
                      id="search"
                      name="search"
                      className="form-control"
                      placeholder="Search Cutomer with 3 character"
                    />
                  </div>
                  <button
                    className="btn btn-danger ms-3"
                    onClick={() => setAddHub(true)}
                  >
                    {" "}
                    <em className="icon me-2 ni ni-plus" /> Add Hub /
                    Distributor
                  </button>
                </div>
              </div>
              <div className="nk-header-tools d-flex align-items-center">
                <ul className="nk-quick-nav me-3">
                  <Dropdown as="li" className="user-dropdown">
                    <Dropdown.Toggle
                      as="a"
                      className="me-n1"
                      id="dropdown-basic"
                    >
                      <div className="user-toggle">
                        <div className="user-avatar sm">
                          <em className="icon ni ni-user-alt" />
                        </div>
                        <div className="user-info d-none d-xl-block">
                          {/* <div className="user-status user-status-unverified">Unverified</div> */}
                          <div className="user-name dropdown-indicator">
                            {getFullName(
                              userData?.firstName,
                              userData?.lastName
                            )}
                          </div>
                          <span>{checkValidData(userData?.email)}</span>
                        </div>
                      </div>
                    </Dropdown.Toggle>
                    <Dropdown.Menu align="end" className="dropdown-menu-md">
                      <div className="dropdown-inner">
                        <ul className="link-list">
                          <li>
                            <Link
                              to={adminRouteMap.ADMIN_PROFILE.path}
                              onClick={() => {
                                document.body.click();
                              }}
                            >
                              <em className="icon ni ni-user-alt" />
                              <span>View Profile</span>
                            </Link>
                          </li>
                          <li>
                            <Link
                              to={adminRouteMap.ADMIN_CHANGE_PASSWORD.path}
                              onClick={() => {
                                document.body.click();
                              }}
                            >
                              <em className="icon ni ni-lock-alt-fill" />
                              <span>Change Password</span>
                            </Link>
                          </li>
                        </ul>
                      </div>
                      <div className="dropdown-inner">
                        <ul className="link-list">
                          <li>
                            <Link
                              to="/"
                              onClick={(e) => {
                                e.preventDefault();
                                showSweetAlert();
                              }}
                            >
                              <em className="icon ni ni-signout" />
                              <span>Sign out</span>
                            </Link>
                          </li>
                        </ul>
                      </div>
                    </Dropdown.Menu>
                    <SweetAlert
                      reverseButtons
                      title="Logout"
                      text="Are you sure want to logout?"
                      show={isAlertVisible}
                      icon="warning"
                      showCancelButton
                      cancelButtonText="No"
                      confirmButtonText="Yes"
                      setIsAlertVisible={setIsAlertVisible}
                      showLoaderOnConfirm
                      loading={loading}
                      onConfirmAlert={onConfirmAlert}
                    />
                  </Dropdown>
                </ul>
                <em className="icon ni ni-more-v" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <ModalComponent
        show={addHub}
        size="lg"
        onHandleCancel={handleClose}
        title="Add Hub"
      >
        {/* <Row>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Hub Name</label>
              <input
                id="hubname"
                name="hubname"
                placeholder="Hub Name"
                className="form-control" 
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Display Name</label>
              <input
                id="dhubname"
                name="dhubname"
                placeholder="Hub Display Name"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Address 1</label>
              <input
                id="address1"
                name="address1"
                placeholder="Address 1"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Address 2</label>
              <input
                id="address2"
                name="address2"
                placeholder="Address 2"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Country</label>
              <input
                id="country"
                name="country"
                placeholder="Country"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">State</label>
              <input
                id="state"
                name="state"
                placeholder="State"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">City</label>
              <input
                id="city"
                name="city"
                placeholder="City"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Mobile No</label>
              <input
                id="mobile"
                name="mobile"
                placeholder="Mobile No"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Landline No</label>
              <input
                id="landline"
                name="landline"
                placeholder="Landline No"
                className="form-control"
              />
            </div>
          </Col>
          <Col md={6}>
            <div className="form-group mb-3">
              <label className="form-label">Website</label>
              <input
                id="website"
                name="website"
                placeholder="Website"
                className="form-control"
              />
            </div>
          </Col>
        </Row> */}
        <Formik
          initialValues={{ ...initialValues }}
          validationSchema={validation()}
          onSubmit={addHubDistributor}
          //   innerRef={formRef}
        >
          {(props) => {
            return (
              <Form>
                <Row>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Hub Name</label>
                      {/* <input
                        id="hubname"
                        name="hubname"
                        placeholder="Hub Name"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="name"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col>
                  {/* <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Display Name</label>
                      <input
                        id="dhubname"
                        name="dhubname"
                        placeholder="Hub Display Name"
                        className="form-control"
                      />
                      <TextInput
                        className="form-control shadow-none"
                        name="fullName"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col> */}
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Address 1</label>
                      {/* <input
                        id="address1"
                        name="address1"
                        placeholder="Address 1"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="address1"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Address 2</label>
                      {/* <input
                        id="address2"
                        name="address2"
                        placeholder="Address 2"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="address2"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Country</label>
                      {/* <input
                        id="country"
                        name="country"
                        placeholder="Country"
                        className="form-control"
                      /> */}
                      <Select
                        id="countryId"
                        name="countryId"
                        // disabled={false}s
                        variant="standard"
                        arrayOfData={countryData}
                        placeholder="Select Country"
                        setFieldValue={props.handleChange}
                        // loanLoading={loanLoading}
                        onSelectChange={(e) => setCountryId(e)}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">State</label>
                      <Select
                        id="stateId"
                        name="stateId"
                        // disabled={false}s
                        variant="standard"
                        arrayOfData={stateData}
                        placeholder="Select State"
                        setFieldValue={props.handleChange}
                        // loanLoading={loanLoading}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">City</label>
                      {/* <input
                        id="city"
                        name="city"
                        placeholder="City"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="city"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Mobile No</label>
                      {/* <input
                        id="mobile"
                        name="mobile"
                        placeholder="Mobile No"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="mobileNo"
                        // disabled={false}
                        variant="standard"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                        min="0"
                        inputmode="numeric"
                        onKeyPress={(e) => {
                          // onCheckValue(e);
                          enterOnlyNumericValue(e);
                        }}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Landline No</label>
                      {/* <input
                        id="landline"
                        name="landline"
                        placeholder="Landline No"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="landlineNo"
                        // disabled={false}
                        variant="standard"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                        min="0"
                        inputmode="numeric"
                        onKeyPress={(e) => {
                          // onCheckValue(e);
                          enterOnlyNumericValue(e);
                        }}
                      />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="form-group mb-3">
                      <label className="form-label">Website</label>
                      {/* <input
                        id="website"
                        name="website"
                        placeholder="Website"
                        className="form-control"
                      /> */}
                      <TextInput
                        className="form-control shadow-none"
                        name="website"
                        // disabled={false}
                        variant="standard"
                        type="text"
                        placeholder="Enter hub Name"
                        setFieldValue={props.handleChange}
                      />
                    </div>
                  </Col>
                </Row>
                <div className="d-flex align-items-center justify-content-end mt-2">
                  {/* <button
                    className="btn btn-md btn-light me-3"
                    onClick={() => setAddHub(false)}
                  >
                    Cancel
                  </button> */}
                  {/* <button className="btn btn-md btn-primary">Submit</button> */}
                  <CommonButton
                    extraClassName="btn btn-md btn-light me-3"
                    // htmlType="submit"
                    // type="submit"
                    onClick={() => setAddHub(false)}
                  >
                    Cancel
                  </CommonButton>
                  <CommonButton
                    extraClassName="btn btn-md btn-primary"
                    htmlType="submit"
                    type="submit"
                  >
                    Submit
                  </CommonButton>
                </div>
              </Form>
            );
          }}
        </Formik>
      </ModalComponent>
    </>
  );
}

export default AdminHeader;
