import { Hub } from "../../../pages";
import adminRouteMap from "../../../routeControl/adminRouteMap";

export default function route() {
  return [
    {
      path: adminRouteMap.HUB.path,
      name: "Hubs / Distributors",
      key: adminRouteMap.HUB.path,
      private: true,
      belongsToSidebar: true,
      icon: adminRouteMap.HUB.icon,
      element: <Hub />,
    },
    {
      path: adminRouteMap.USER_DETAILS.path,
      name: "User Details",
      key: adminRouteMap.USER_DETAILS.path,
      private: true,
      element: <></>,
    },
  ];
}
