import { ProductCategory } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const productServices = {
  getProductCategoryList: async (queryParams) => {
    try {
      const payload = {
        ...ProductCategory.allProductCategory,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
