import DashboardRoutes from "./Dashboard/index.route";
import HubRoutes from "./Hub/index.route";
import adminAccountRoutes from "./AdminAccount/index.route";
import customerRoutes from "./Customer/index.route";
import regionsRoutes from "./Regions/index.route";
import locationsRoutes from "./Locations/index.route";
import productCategoryRoutes from "./ProductCategory/index.route";
import CommunicationsRoutes from "./Communications/index.route";
import BrandsRoutes from "./Brands/index.route";
import ProductPackageSorting from "./Productpack/index.route";
import Banner from "./Banner/index.route";

export default function route() {
  return [
    ...DashboardRoutes(),
    ...HubRoutes(),
    ...adminAccountRoutes(),
    ...customerRoutes(),
    ...regionsRoutes(),
    ...locationsRoutes(),
    ...productCategoryRoutes(),
    ...CommunicationsRoutes(),
    ...BrandsRoutes(),
    ...Banner(),
    ...ProductPackageSorting(),
  ];
}
