import { Locations } from "../../../pages";
import adminRouteMap from "../../../routeControl/adminRouteMap";

export default function route() {
  return [
    {
      path: adminRouteMap.LOCATIONS.path,
      name: "locations",
      key: adminRouteMap.LOCATIONS.path,
      private: false,
      belongsToSidebar: true,
      icon: adminRouteMap.LOCATIONS.icon,
      element: <Locations />,
    },
  ];
}
