import { Banner } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const bannerServices = {
  getBannerList: async (queryParams) => {
    try {
      const payload = {
        ...Banner.allBanner,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  addBanner: async (bodyData) => {
    try {
      const payload = {
        ...Banner.addBanner,
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  updateBrand: async (id, bodyData) => {
    try {
      const payload = {
        ...Banner.updateBanner(id),
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
