import { Regions } from "../../../pages";
import adminRouteMap from "../../../routeControl/adminRouteMap";

export default function route() {
  return [
    {
      path: adminRouteMap.REGIONS.path,
      name: "Regions",
      key: adminRouteMap.REGIONS.path,
      private: false,
      belongsToSidebar: true,
      icon: adminRouteMap.REGIONS.icon,
      element: <Regions />,
    },
  ];
}
