import { Communications } from "../../../pages";
import adminRouteMap from "../../../routeControl/adminRouteMap";

export default function route() {
  return [
    {
      path: adminRouteMap.COMMUNICATIONS.path,
      name: "Communications",
      key: adminRouteMap.COMMUNICATIONS.path,
      private: false,
      belongsToSidebar: true,
      icon: adminRouteMap.COMMUNICATIONS.icon,
      element: <Communications />,
    },
  ];
}
